.header_title {
    margin: 10%;
}

.page_intro {
    font-family: 'Italiana', serif;
    font-size: 50px;
    color:black;
    text-shadow: 2px 2px 2px tan;
}

.form_container {
    display:flex;
    flex-direction: column;
    margin: 50px;
    padding: 30px;
    background-color: oldlace;
    border-radius: 15px;
    border: 3px solid #D5CABD;
    box-shadow: 20px 20px 50px 15px rgb(44, 42, 44);
}

.articleInput {
    font-size: 18px;
    background-color: white;
    padding:15px;
    font-family: 'Poiret One', cursive;
    border-radius: 5%;
    margin-bottom: 3%;
    width: 100%;
   
}

label {
    margin: 10px;
    font-size: 20px;
    font-weight: bolder;
    font-family: 'Poiret One', cursive;
}

.card_btn2{
    width:40%;
    font-family: 'Poiret One', cursive;
    font-weight: 600;
    color:white;
    background-color: #679985;
    border: none;
    border-radius: 15px;
    padding: 15px;
    margin: 10px 0 0 70px;
    cursor: pointer;
}